input[type="radio"]{height:0;width:0;visibility:hidden}input[type="radio"]:checked+label:after{transform:scale(4.2);mix-blend-mode:difference;background:radial-gradient(circle at center, orange 30%, #000 100%)}label{outline:none;user-select:none;color:#f2f2f2;font-size:1.5rem;width:6rem;height:4rem;line-height:4rem;text-align:center;letter-spacing:0.04rem;margin-right:0.5rem;cursor:pointer;border-radius:0.4rem;background:#000;position:relative;overflow:hidden}@media (max-width: 619px){label{width:4rem;height:2.75rem;font-size:1rem;line-height:2.75rem;margin-right:0.2rem}}label::after{content:"";position:absolute;width:100%;height:100%;top:0;left:0;transform:scale(0);transition:transform .3s ease-in-out}label:active{top:3px;box-shadow:none}:root{--display-style: block}html,body{font-size:13px;overflow:hidden}@media (min-width: 620px){html,body{font-size:15px}}@media (min-width: 1024px){html,body{font-size:20px}}*{margin:0;padding:0;box-sizing:border-box;font-family:"Lato", sans-serif}code{font-family:source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace}main{height:100vh}@keyframes flicker{0%{opacity:0}50%{opacity:0}70%{opacity:1}}.flicker{animation:flicker 1s infinite;text-decoration:underline}
